import React from 'react'
import Box from '@material-ui/core/Box'
import { makeStyles } from '@material-ui/core/styles'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'

const useStyles = makeStyles({
    root: {
      width: 730,
      maxWidth: '100%',
    },
  });

const Thework: React.FC = () => {
    const classes = useStyles();

    return (
        <>
            <Box p={2}>
                <Box style={{ display: 'flex', justifyContent: 'center'}} p={1}>
                    <Typography variant='h5'>
                        取り組んできたこと、作ったもの
                    </Typography>
                </Box>

                <Box style={{ display: 'flex', justifyContent: 'center'}} p={1}>
                    <Card className={classes.root}>
                        <CardContent>
                            <Typography variant="h6">
                                {/* ここに書く */}
                                ・Discord.pyを使ってTwitterのツイートを取得するBOT<br />
                                ・SeleniumやBeautifulsoupを使ったスクレイピングツールの作成<br />
                                ・Pandasやmatplotlibを使ったデータ分析
                                ・ReactとTypeScriptを使ったポートフォリオサイト(このサイト)の作成<br />
                                ・Atcoderでアルゴリズム能力の鍛えるために過去問を解いた。<br />
                                ・TryHackMeなど使って学習し、HackTheBoxで1つUserまで解いた。<br />
                                ※Github等にあげてないためソースコードはありません。
                                
                            </Typography>
                        </CardContent>
                    </Card>
                </Box>
            </Box>
        </>
    )
}

export default Thework;